@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar img{
  width: 50px;
  height: 45px;
}
.navbar a {
  margin-left: 16px;
  color: #f1356d;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: darkred;
}
.content {
  width: 50%;
  margin: 40px auto;
  padding: 20px;
}

.exercise-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.exercise-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.exercise-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.help{
  position: absolute;
  bottom: 0;
}

.help button{
  background-color:#f1356d;
}

.rsc{

  margin: 10px;
  background: #f1356d;

} 
.sc-gqjmRU.glfuN.rsc-header{
  background: #f1356d;
  color:white;
}

.sc-bZQynM.hQsUiY.rsc-ts-bubble{
  background: #f1356d;
}

.sc-bZQynM.cRmLCo.rsc-ts-bubble{
  background: #f1356d;
}

.exercise-preview a {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  text-decoration: none;
  cursor: pointer;
}
.exercise-preview a:hover {
  color: darkred;
}

.exercise-details h2 {
  font-size: 20px;
  color:#f1356d;
  margin-bottom: 10px;
}

.exercise-details div {
  margin: 20px 0;
}

.exercise-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.signup {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.signup label {
  text-align: left;
  display: block;
}
.signup h2 {
  font-size: 30px;
  color: #f1356d;
  margin-bottom: 30px;
}
.signup input, .signup textarea, .signup select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.signup button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 8px;
  cursor: pointer;
}

.not-found h1{
  font-size: 40px;
  color: #f1356d;
  margin-bottom: 30px;
  text-align: center;
}

.not-found a {
  margin-left: auto;
  margin-right: auto;
  text-decoration: none;
  padding: 6px;
  color:white;
  background-color: #f1356d;
  border-radius: 8px;
}
.not-found a:hover {
  color: darkred;
}

.welcome{
  text-align: center;
}

.welcome h2{

  font-size: 40px;
  color: #f1356d;
  margin-bottom: 30px;

}

.welcome a{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  font-size: 20px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  display: block;
  
}
.welcome a:hover{
  color:darkred;
}

.menu a{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
}

.menu a:hover{
  color:darkred;
}

.card-img-top{
  height: 300px;
}

.card-title{
  
  font-size: 40px;
  color: #f1356d;
  margin-bottom: 30px;
}

.premiumq{
  text-align: center;
}

.premiumq button{
  margin: 10px;
}

.signup button:hover{
  color: darkred;
}

button.skip{
  background-color: white;
  color:#f1356d;
  margin: 5px;
  border: 1px solid #f1356d;
}


.premiumq button.skip:hover{
  color: darkred;
}

.premiumq h3{
  text-align: center;
  font-size: 22px;
}

.premiumq li{
  text-align: left;
  color:black;
  font-size: large;
  font-weight: 600;
  
}


